<template>
    <div class="m-extend">
        <!-- 群号 -->
        <RightSideMsg>
            <em>休闲党交流群</em> :
            <strong
                ><a href="https://jq.qq.com/?_wv=1027&k=InbtJGcw"
                    >658035079</a
                ></strong
            >
        </RightSideMsg>

        <!-- 排行榜 -->
        <div class="m-side-rank">
            <h3 class="c-sidebar-right-title">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g fill="#ffa91e"><path d="M324.891 167.266l16.262 94.815L256 217.314l-85.153 44.767 16.262-94.815-68.891-67.16 95.205-13.833L256 0l42.577 86.273 95.205 13.833z"/><path d="M409.944 253.46v87.673L256.9 403.303l-.9-.36-153.944-61.76V253.46L256 315.219z"/><path d="M409.944 362.157v87.673L256.9 512l-.9-.36-153.944-61.76v-87.723L256 423.916z"/></g><g fill="#ff8900"><path d="M341.153 262.081L256 217.314V0l42.577 86.273 95.205 13.833-68.891 67.16zM409.944 253.46v87.673L256.9 403.303l-.9-.36v-87.724zM256 423.916l153.944-61.759v87.673L256.9 512l-.9-.36z"/></g></svg>
                热门词条
                <!-- <router-link class="u-more" to="/rank">完整榜单 &raquo;</router-link> -->
            </h3>
            <ul class="m-rank-list" v-if="data.length">
                <li v-for="(item, i) in data" :key="i">
                    <a class="u-link" :href="item.id | postLink">
                        <span class="u-order" :class="highlight(i)">{{
                            i + 1
                        }}</span>
                        <span class="u-name"
                            >{{ item.title }}</span
                        >
                        <span class="u-per">
                            <em class="u-count">+ {{ views[i] }}</em>
                        </span>
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { getRank } from "@/service/stat.js";
import { getRankPost } from "@/service/post.js";
import { postLink} from '@jx3box/jx3box-common/js/utils'
export default {
    name: "Extend",
    props: [],
    data: function() {
        return {
            data: "",
            views: [],
        };
    },
    computed: {},
    methods: {
        highlight : function (i){
            if(i == 0){
                return 't1'
            }else if(i == 1){
                return 't2'
            }else if(i == 2) {
                return 't3'
            }
        },
    },
    filters : {
        postLink : function (id){
            return postLink('wiki',id)
        }
    },
    mounted: function() {
        getRank().then((res) => {
            let result = res.data;
            let list = [];
            result.forEach((item) => {
                if (item.name.startsWith("wiki")) {
                    let id = item.name.split("-").pop();
                    list.push(id);
                    this.views.push(item.value["7days"]);
                }
            });
            getRankPost(list.join(",")).then((res) => {
                this.data = res.data.data.data;
                // console.log(this.data);
            });
        });
    },
    components: {},
};
</script>

<style lang="less">
@import "../assets/css/side.less";
